import { mapState } from 'vuex';
import {host} from '@/pages/utils/request'
var qcloud = require("wafer2-client-sdk/index.js")
export default{
	props:{
		modalName: String,
		
	},
	mounted(){
		qcloud.setLoginUrl(host + "/api/login")
	},
	computed:{
	...mapState(['hasLogin' ,'tempLogin'])
	},
	data(){
		return{
			login : '/static/wx_login.png',
		}
	},
	methods:{
		async confirm(e){
			if(e.detail.userInfo){
				wx.showLoading({
				  title: "登录中...", //提示的内容,
				  mask: true, //显示透明蒙层，防止触摸穿透,
				  success: res => {}
				});
				const session = qcloud.Session.get();
				if (session) {
				  // 第二次登录
				  // 或者本地已经有登录态
				  // 可使用本函数更新登录态
				  qcloud.loginWithCode({
				    success: res => {
				      // this.setData({ userInfo: res, logged: true });
				     // wx.setStorageSync("key", "value")
					 this.$store.commit('vuelogin',res)
					 this.$emit('tologin')
				    },
				    fail: err => {
				      console.error(err)
				    }
				  });
				} else {
				 qcloud.login({
					 success: res => {
					   wx.hideLoading()
					   this.$store.commit('vuelogin',res)
					   this.$emit('hiddendiag')
					   console.log(res)
					   }
					})
				}		
			}else{
				this.$store.commit('tempLogin')
				this.$emit('hiddendiag') //不同意 就给一个临时访问token 
			}
		},
		hideModal(){
			this.$store.commit('tempLogin')
			this.$emit('hiddendiag')  //不同意 就给一个临时访问token ,退出就失效
			
		},		
	}
}
